import React, { useState, forwardRef, useImperativeHandle } from 'react';
import axios from 'axios';

const Register = forwardRef(({ onRegisterSuccess }, ref) => {
    const [username, setUsername] = useState('');
    const [password, setPassword] = useState('');
    const [errorMessage, setErrorMessage] = useState('');

    const handleRegister = async () => {
        setErrorMessage(''); // Reset error message

        // Frontend validation
        if (!username || !password) {
            setErrorMessage('All fields are required');
            return;
        }

        try {
            const response = await axios.post('http://localhost:5222/api/authentication/register', { username, password });
            console.log('Registration successful', response);
            if (onRegisterSuccess) onRegisterSuccess();
        } catch (error) {
            console.error('Registration failed', error);
            if (error.response) {
                const errorMessage = error.response.data.errors || error.response.data.Result || 'Unknown error';
                setErrorMessage(`Registration failed: ${errorMessage}`);
            } else {
                setErrorMessage('Registration failed: Network error');
            }
        }
    };

    const handleKeyPress = (e) => {
        if (e.key === 'Enter') {
            handleRegister();
        }
    };

    useImperativeHandle(ref, () => ({
        clearFields() {
            setUsername('');
            setPassword('');
            setErrorMessage('');
        }
    }));

    return (
        <div className="container">
            <h2>Register</h2>
            <div className="form-group">
                <input
                    type="text"
                    className="form-control"
                    placeholder="Username"
                    value={username}
                    onChange={(e) => setUsername(e.target.value)}
                    onKeyPress={handleKeyPress}
                />
            </div>
            <div className="form-group">
                <input
                    type="password"
                    className="form-control"
                    placeholder="Password"
                    value={password}
                    onChange={(e) => setPassword(e.target.value)}
                    onKeyPress={handleKeyPress}
                />
            </div>
            {errorMessage && (
                <div className="alert alert-danger" role="alert">
                    {errorMessage}
                </div>
            )}
            <button className="btn btn-primary" onClick={handleRegister}>Register</button>
        </div>
    );
});

export default Register;
