import React, { useState, forwardRef, useImperativeHandle } from 'react';
import axios from 'axios';

const Login = forwardRef(({ setToken, setUser }, ref) => {
    const [username, setUsername] = useState('');
    const [password, setPassword] = useState('');
    const [errorMessage, setErrorMessage] = useState('');

    const handleLogin = async () => {
        setErrorMessage(''); // Reset error message
        try {
            const response = await axios.post('http://localhost:5222/api/authentication/login', { username, password });
            setToken(response.data.token);
            localStorage.setItem('token', response.data.token);
            setUser(username);
        } catch (error) {
            console.error('Login failed', error);
            if (error.response && error.response.data) {
                const errorMessage = error.response.data.result || 'Unknown error';
                setErrorMessage(`Login failed: ${errorMessage}`);
            } else {
                setErrorMessage('Login failed: Network error');
            }
        }
    };

    const handleKeyPress = (e) => {
        if (e.key === 'Enter') {
            handleLogin();
        }
    };

    useImperativeHandle(ref, () => ({
        clearFields() {
            setUsername('');
            setPassword('');
            setErrorMessage('');
        }
    }));

    return (
        <div className="container auth-wrapper">
            <h2>Login</h2>
            <div className="form-group">
                <input
                    type="text"
                    className="form-control"
                    placeholder="Username"
                    value={username}
                    onChange={(e) => setUsername(e.target.value)}
                    onKeyPress={handleKeyPress}
                />
            </div>
            <div className="form-group">
                <input
                    type="password"
                    className="form-control"
                    placeholder="Password"
                    value={password}
                    onChange={(e) => setPassword(e.target.value)}
                    onKeyPress={handleKeyPress}
                />
            </div>
            {errorMessage && (
                <div className="alert alert-danger" role="alert">
                    {errorMessage}
                </div>
            )}
            <button className="btn btn-primary" onClick={handleLogin}>Login</button>
        </div>
    );
});

export default Login;
