import React, { useEffect, useState, useRef } from 'react';
import * as signalR from '@microsoft/signalr';
import 'bootstrap/dist/css/bootstrap.min.css';
import './index.css';
import Login from './Login';
import Register from './Register';

function App() {
    const [connection, setConnection] = useState(null);
    const [messages, setMessages] = useState([]);
    const [user, setUser] = useState(localStorage.getItem('username') || '');
    const [message, setMessage] = useState('');
    const [typingUsers, setTypingUsers] = useState([]);
    const [token, setToken] = useState(localStorage.getItem('token') || '');
    const [showLogin, setShowLogin] = useState(true);

    const loginRef = useRef();
    const registerRef = useRef();
    const typingTimeoutRef = useRef(null);
    const chatContainerRef = useRef(null);

    useEffect(() => {
        if (token) {
            const newConnection = new signalR.HubConnectionBuilder()
                .withUrl('http://localhost:5222/chatHub', { accessTokenFactory: () => token })
                .withAutomaticReconnect()
                .build();

            setConnection(newConnection);
        }
    }, [token]);

    useEffect(() => {
        if (connection) {
            connection.start()
                .then(result => {
                    console.log('Connected!');

                    connection.off('ReceiveMessage');
                    connection.off('UserTyping');
                    connection.off('UserStopTyping');

                    connection.on('ReceiveMessage', (user, message) => {
                        const newMessage = { user, message };
                        setMessages(prevMessages => {
                            const updatedMessages = [...prevMessages, newMessage];
                            localStorage.setItem('chatMessages', JSON.stringify(updatedMessages));
                            return updatedMessages;
                        });
                    });

                    connection.on('UserTyping', (user) => {
                        setTypingUsers(users => {
                            if (!users.includes(user)) {
                                return [...users, user];
                            }
                            return users;
                        });
                    });

                    connection.on('UserStopTyping', (user) => {
                        setTypingUsers(users => users.filter(u => u !== user));
                    });
                })
                .catch(e => console.log('Connection failed: ', e));
        }
    }, [connection]);

    useEffect(() => {
        const storedMessages = localStorage.getItem('chatMessages');
        if (storedMessages) {
            setMessages(JSON.parse(storedMessages));
        }
    }, []);

    useEffect(() => {
        if (chatContainerRef.current) {
            chatContainerRef.current.scrollTop = chatContainerRef.current.scrollHeight;
        }
    }, [messages]);

    const handleInputChange = (e) => {
        setMessage(e.target.value);
        if (connection && connection.state === signalR.HubConnectionState.Connected) {
            connection.send('TypingNotification', user);
            if (typingTimeoutRef.current) {
                clearTimeout(typingTimeoutRef.current);
            }
            typingTimeoutRef.current = setTimeout(() => {
                connection.send('StopTypingNotification', user);
            }, 3000);
        }
    };

    const handleKeyPress = async (e) => {
        if (e.key === 'Enter') {
            await sendMessage();
        }
    };

    const sendMessage = async () => {
        if (connection && connection.state === signalR.HubConnectionState.Connected && message.trim()) {
            try {
                await connection.send('SendMessage', user, message);
                setMessage('');
                connection.send('StopTypingNotification', user);
            } catch (e) {
                console.log(e);
            }
        } else if (!message.trim()) {
          console.log("Message is empty");

        } else {
            alert('No connection to server yet.');
        }
    };

    const clearMessages = () => {
        setMessages([]);
        localStorage.removeItem('chatMessages');
    };

    const handleRegisterSuccess = () => {
        setShowLogin(true);
        if (loginRef.current) loginRef.current.clearFields();
    };

    const handleSwitchToRegister = () => {
        setShowLogin(false);
        if (registerRef.current) registerRef.current.clearFields();
    };

    const handleSwitchToLogin = () => {
        setShowLogin(true);
        if (loginRef.current) loginRef.current.clearFields();
    };

    const handleLogout = () => {
        setToken('');
        localStorage.removeItem('token');
        localStorage.removeItem('username');
        setUser('');
        if (connection) {
            connection.stop();
        }
    };

    const handleSetUser = (username) => {
        setUser(username);
        localStorage.setItem('username', username);
    };

    const truncateUsername = (username, maxLength = 20) => {
        return username.length > maxLength ? `${username.substring(0, maxLength)}...` : username;
    };

    if (!token) {
        return (
            <div className="container">
                <div style={{ display: showLogin ? 'block' : 'none' }}>
                    <Login ref={loginRef} setToken={setToken} setUser={handleSetUser} />
                    <p className="register">
                        Don't have an account? <button onClick={handleSwitchToRegister} className="btn btn-link">Register here</button>
                    </p>
                </div>
                <div style={{ display: showLogin ? 'none' : 'block' }}>
                    <Register ref={registerRef} onRegisterSuccess={handleRegisterSuccess} />
                    <p className="register">
                        Already have an account? <button onClick={handleSwitchToLogin} className="btn btn-link">Login here</button>
                    </p>
                </div>
            </div>
        );
    }

    return (
        <div className="container">
            <div className="chat-wrapper">
                <div className="welcome-message">
                    Welcome, {truncateUsername(user)}
                </div>
                <div className="form-group">
                    <input
                        type="text"
                        className="form-control"
                        placeholder="Message"
                        value={message}
                        onChange={handleInputChange}
                        onKeyPress={handleKeyPress}
                    />
                </div>
                <div className="buttons-container">
                    <button onClick={sendMessage} className="btn btn-primary">Send</button>
                    <button onClick={clearMessages} className="btn btn-danger">Clear Chats</button>
                    <button onClick={handleLogout} className="btn btn-secondary">Logout</button>
                </div>
                <div className="chat-container" ref={chatContainerRef}>
                    {messages.map((m, index) => (
                        <div key={index} className="chat-message">
                            <strong className="message-username">{truncateUsername(m.user)}:</strong> {m.message}
                        </div>
                    ))}
                    <div>
                        {typingUsers.map((user, index) => (
                            <div key={index} className="typing-user">{truncateUsername(user)} is typing...</div>
                        ))}
                    </div>
                </div>
            </div>
        </div>
    );
}

export default App;
